<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class="top-main">
                                <div class="page-top-line">
                                    <div class="page-title">资源监控</div>
                                </div>
                            </div>
                            <div class="out-div">
                                <div class="res-list">
                                    <template>
                                        <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
                                            <el-table-column prop="userName" label="训练任务名称"></el-table-column>
                                            <el-table-column prop="userAccount" label="发起节点"></el-table-column>
                                            <el-table-column prop="userType" label="训练轮次"></el-table-column>
                                            <el-table-column prop="institution" label="样本量"></el-table-column>
                                            <el-table-column prop="institution" label="当前状态"></el-table-column>
                                        </el-table>
                                        <el-pagination
                                            @size-change="handleSizeChange"
                                            @current-change="handleCurrentChange"
                                            :current-page="currentPage"
                                            :page-sizes="[5,10,20,50]"
                                            :page-size="pagesize"
                                            layout="total, sizes, prev, pager, next, jumper"
                                            :total="tableData.length"
                                            style="margin-top:15px">
                                        </el-pagination>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
            </el-main>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/account.css";
</style>
<script>
import $ from "jquery";
import Header from "../sub/aside";
import containerTop from "../sub/container_top";
export default {
    name: "homepage",
    components: {
        "nav-header": Header,
        "container-top": containerTop,
    },
    data: function() {
        return {
            username:Cookies.get("fedname"),
            tableData: [{"userName":"amao","userAccount":"13888888888","userType":"节点管理员","institution":"余杭农商行"},
            {"userName":"wangc","userAccount":"13888888888","userType":"节点普通用户","institution":"中国移动"}],
            pagesize:10,
            currentPage:1,
        }
    },
    mounted(){
        this.findDimensions()
    },
    methods: {
        handleSizeChange: function (size) {
            this.pagesize = size;
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
        },
        findDimensions:function(){
            if(window.innerWidth)
            this.winWidth = window.innerWidth;
            else if ((document.body) && (document.body.clientWidth))
            this.winWidth = document.body.clientWidth;
            //获取窗口高度
            if(window.innerHeight)
            this.winHeight = window.innerHeight;
            else if((document.body) && (document.body.clientHeight))
            this.winHeight = document.body.clientHeight;
            //通过深入Document内部对body进行检测，获取窗口大小
            if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                this.winHeight = document.documentElement.clientHeight;
                this.winWidth = document.documentElement.clientWidth;
            }
            $(".res-list").attr("style","min-height:"+(this.winHeight-290)+"px;")
        },
    }
};
</script>



